@import url('https://fonts.googleapis.com/css2?family=Calistoga&family=Voces&display=swap');

*{
  box-sizing: border-box;  
}

h1{  
  font-size:3rem;  
  margin:0;
  font-family: 'Calistoga', cursive;
}

body{
  margin:0;
  background-color: #ffffd4;
  font-family: 'Voces', cursive;
}

button{
  font-family: 'Voces', cursive;
}

.Header{
  background-color: #ffe3a2;
  text-align:center;
  padding:20px;
}

.pets-container{
  display:flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}

.pet-container{
  width:370px;
  padding:15px 30px 15px 30px;
}

.pet-image{
  max-width:100%;
  max-height:100%;
}

ul{  
  list-style: none;
  padding:0;
  margin:0;
}

.app-description{
  width:370px;
  text-align:left;
  font-size:1.5rem;
  padding:10px;
}

.landing-Page{
  display:flex;
  flex-direction: column;
  align-items: center;
}

.landing-image{
  padding:10px;
  width:370px;
  height:560px;
}

fieldset{
  border:none;
}

.adoption-page-container{
  display:flex;
  flex-direction: column;
  align-items: center;
}
